.cms .branded-menu--child {
  padding-left: 0; }

.cms .branded-menu__list-item {
  list-style: none; }
  .cms .branded-menu__list-item--children > a:first-child {
    position: relative; }
  .cms .branded-menu__list-item--children a {
    padding: 16px 5px 16px 8px !important; }
  .cms .branded-menu__list-item .text {
    margin-left: 30px !important; }
    @media (max-width: 767px) {
      .cms .branded-menu__list-item .text {
        margin-left: 38px !important; } }
  .cms .branded-menu__list-item .toggle-children {
    float: none;
    position: absolute;
    padding: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .cms .branded-menu__list-item .toggle-children-icon {
      border-color: currentColor;
      position: absolute;
      top: 50%;
      left: 21px;
      margin-left: -5.5px;
      margin-top: -5.5px; }
      @media (max-width: 767px) {
        .cms .branded-menu__list-item .toggle-children-icon {
          left: 28px; } }

.cms .cms-menu__list li.current > a .toggle-children .toggle-children-icon {
  width: 11px;
  height: 11px;
  background: none; }

.cms-logo {
  display: block;
  text-align: center;
  padding: 0 8px; }
  .cms-logo > img {
    max-width: 100%;
    height: auto; }

.cms .cms-sitename__title {
  margin-left: 12px; }

@keyframes infinite-spinning {
  from {
    transform: translate3d(-50%, -50%, 0) rotate(0deg); }
  to {
    transform: translate3d(-50%, -50%, 0) rotate(360deg); } }

/*
|--------------------------------------------------------------------------
| Loading Logo
|--------------------------------------------------------------------------
*/
.ss-loading-screen .loading-logo {
  background: transparent none; }
  .ss-loading-screen .loading-logo::after {
    position: absolute;
    content: "";
    display: block;
    width: 3rem;
    height: 3rem;
    background: transparent none;
    border: 2px solid rgba(0, 0, 0, 0.04);
    border-top-color: #0071c4 !important;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    animation: infinite-spinning 800ms linear infinite; }
  .ss-loading-screen .loading-logo .loading-animation {
    display: none !important; }

.cms-content-loading-spinner {
  background-image: none; }
  .cms-content-loading-spinner::after {
    position: absolute;
    content: "";
    display: block;
    width: 3rem;
    height: 3rem;
    background: transparent none;
    border: 2px solid rgba(0, 0, 0, 0.04);
    border-top-color: #0071c4 !important;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    animation: infinite-spinning 800ms linear infinite; }

.cms .sticky-status-indicator {
  left: 26px;
  transform: translate3d(-50%, 0, 0); }

.cms .cms-menu.collapsed .sticky-status-indicator {
  left: 20px; }

.cms .sticky-toggle {
  position: relative;
  background-image: none; }
  .cms .sticky-toggle.active {
    background-image: none; }
    .cms .sticky-toggle.active::before {
      display: block; }
  .cms .sticky-toggle::before, .cms .sticky-toggle::after {
    content: "";
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: transparent;
    border: 1px solid currentColor;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0); }
  .cms .sticky-toggle::before {
    display: none;
    width: 7px;
    height: 7px;
    background: currentColor;
    border-width: 0; }
